     border-top: 1px solid #ddd;
   }
   
   .contact-inf {
     display: flex;
     gap: 12px;
   }
   
   .project-container {
     width: 100%;
     max-width: 1200px;
     margin: 0 auto;
     padding: 20px;
     background-color: #fff;
   }
   
   h1 {
     text-align: center;
     margin-bottom: 20px;
   }
   
   .nav-buttons {
     display: flex;
     margin: 70px 0 50px 20px;
   }
   
   .nav-button {
     display: block;
     padding: 10px 20px;
     margin: 0 10px;
     background-color: #FACA4E;
     color: #fff;
     text-align: center;
     text-decoration: none;
     border-radius: 50px;
     font-weight: bold;
   }
   
   .nav-button:hover {
     background-color: #FF981B;
   }
   
   .active {
     background-color: #e67e22;
   }
   
   .search-container {
     display: flex;
     align-items: center;
     margin-bottom: 30px;
     justify-content: space-between;
   }
   
   .search-box {
     display: flex;
     align-items: center;
   }
   
   .category-select,
   .search-input {
     padding: 10px;
     border: 1px solid #ccc;
     border-radius: 5px;
     margin-right: 10px;
   }
   
