<template>
  <!-- 고정 페이지 최상단 영역 -->
  
        <!-- 헤더 영역 메뉴바 끝 -->
    <div class="application-container">
      <h1>지원신청</h1>
      <form @submit.prevent="submitForm" enctype="multipart/form-data">
        <table>
          <tr>
            <td>이름</td>
            <td><input type="text" v-model="form.name" required /></td>
          </tr>
          <tr>
            <td>이메일</td>
            <td><input type="email" v-model="form.email" required /></td>
          </tr>
          <tr>
            <td>휴대폰</td>
            <td><input type="tel" v-model="form.phone" required /></td>
          </tr>
          <tr>
            <td>개인정보동의</td>
            <td>
              <input type="checkbox" v-model="form.agree" required />
              <span>입력하신 내용은 지원 외에 다른 용도로 사용되지 않습니다.<br />개인정보 동의하시면 체크해주세요.</span>
            </td>
          </tr>
          <tr>
            <td>내용</td>
            <td><textarea v-model="form.content" required></textarea></td>
          </tr>
          <tr>
            <td>첨부파일1</td>
            <td>
              <input type="file" @change="handleFileChange($event, 'file1')" />
              <span v-if="form.file1">{{ form.file1.name }}</span>
              <span v-else>선택된 파일 없음 | 첨부파일1에 지원서를 첨부해주세요</span>
            </td>
          </tr>
          <tr>
            <td>첨부파일2</td>
            <td>
              <input type="file" @change="handleFileChange($event, 'file2')" />
              <span v-if="form.file2">{{ form.file2.name }}</span>
              <span v-else>선택된 파일 없음 | 첨부파일2에 개인정보 수집 이용 동의서를 첨부해주세요</span>
            </td>
          </tr>
        </table>
        <button type="submit">지원 신청하기</button>
      </form>
    </div>


  </template>
  
  <script setup>
  import { ref } from 'vue';

  const form = ref({
    name: '',
    email: '',
    phone: '',
    agree: false,
    content: '',
    file1: null,
    file2: null,
  });
  
  const handleFileChange = (event, fileKey) => {
    form.value[fileKey] = event.target.files[0];
  };
  
  const submitForm = () => {
    const formData = new FormData();
    formData.append('name', form.value.name);
    formData.append('email', form.value.email);
    formData.append('phone', form.value.phone);
    formData.append('agree', form.value.agree);
    formData.append('content', form.value.content);
    if (form.value.file1) formData.append('file1', form.value.file1);
    if (form.value.file2) formData.append('file2', form.value.file2);
  
    // Form submission logic here, for example, using axios to post data to a server
    // axios.post('/api/apply', formData).then(response => {
    //   console.log('Form submitted successfully:', response);
    // }).catch(error => {
    //   console.error('Error submitting form:', error);
    // });
    console.log('Form data:', formData);
  };
  </script>

<script>
import Header from '@/HFfile/Header.vue';
import Footer from '@/HFfile/Footer.vue';

export default {
  components: {
    Header,
    Footer
  }
};
</script>
  
  <style scoped>
  .application-container {
    width: 600px;
    margin: 0 auto;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 10px;
  }
  
  table {
    width: 100%;
    border-collapse: collapse;
  }
  
  td {
    padding: 10px;
    border: 1px solid #ddd;
  }
  
  input[type="text"],
  input[type="email"],
  input[type="tel"],
  textarea {
    width: 100%;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  input[type="checkbox"] {
    margin-right: 10px;
  }
  
  button {
    width: 100%;
    padding: 10px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    margin-top: 20px;
  }
  
  button:hover {
    background-color: #0056b3;
  }
  </style>
  