<template>
  <!-- 헤더 영역 메뉴바 끝 -->

  <!-- 모집 공고 게시판 제목 목록 -->
  <h1>모집 공고 게시판</h1>
  <div class="pagination">
    <button @click="prevPage" :disabled="currentPage === 1">Previous</button>
    <span
      v-for="page in totalPages"
      :key="page"
      @click="setPage(page)"
      :class="{ 'active-page': currentPage === page }"
    >
      {{ page }}
    </span>
    <button @click="nextPage" :disabled="currentPage === totalPages">
      Next
    </button>
  </div>

  <!-- 게시물 제목 목록 -->

  <div class="posts-list">
    <div v-for="post in posts" :key="post.id" class="post-item">
      <!-- 제목 클릭 시 상세 페이지로 이동 -->
      <router-link
        :to="{ name: 'RecruitmentNotice1', params: { id: post.id } }"
      >
        {{ post.title }}
      </router-link>
    </div>
  </div>

  <!-- 글 쓰기 버튼 (관리자만) -->
  <button @click.prevent="handleClick">모집 공고 글 쓰기 버튼</button>
  <!-- footer 영억 시작 하단 -->

  <!-- footer 영역 끝 -->

  <router-view />
</template>

<script setup>
import { ref, onMounted } from 'vue';
import axios from 'axios';
import { useRouter } from 'vue-router';
import { useUserStore } from '@/store/SignUp';
import { storeToRefs } from 'pinia';

const router = useRouter();
const userStore = useUserStore();
const { isLoggedIn, isAdmin } = storeToRefs(userStore);

const currentPage = ref(1);
const totalPages = ref(1);
const posts = ref([]);

const fetchPosts = async () => {
  try {
    const response = await axios.get('http://localhost:80/posts', {
      params: { page: currentPage.value },
    });
    posts.value = response.data.posts;
    totalPages.value = response.data.totalPages;
  } catch (error) {
    console.error('Error fetching posts:', error);
  }
};

const prevPage = () => {
  if (currentPage.value > 1) {
    currentPage.value--;
    fetchPosts();
  }
};

const nextPage = () => {
  if (currentPage.value < totalPages.value) {
    currentPage.value++;
    fetchPosts();
  }
};

const setPage = (page) => {
  currentPage.value = page;
  fetchPosts();
};

const handleClick = async () => {
  if (isLoggedIn.value) {
    if (isAdmin.value) {
      router.push({ name: 'PrModRecruitmentNotice' });
    } else {
      alert('관리자만 접근할 수 있습니다.');
    }
  } else {
    alert('로그인 해주세요.');
    await handleLogin();
    router.push({ name: 'Login' });
  }
};

const handleLogin = async () => {
  try {
    const response = await axios.post('http://localhost:80/user/login', {
      username: userStore.username,
      password: userStore.password,
    });

    if (response.data.message) {
      userStore.isLoggedIn = true;
      userStore.isAdmin = response.data.grade === 1;
      router.push({ name: 'RecruitmentNotice' });
    } else {
      console.error('Login failed');
      userStore.isLoggedIn = false;
    }
  } catch (error) {
    console.error('Error logging in user:', error);
    userStore.isLoggedIn = false;
  }
};

const logout = () => {
  userStore.isLoggedIn = false;
  userStore.isAdmin = false;
  router.push({ name: 'Home' });
};

// 초기 데이터 로드
onMounted(() => {
  fetchPosts();
});
</script>

